@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Roboto+Mono:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cabin&display=swap);
body{background-color:#fff}

header{width:100%;height:auto;background-color:#000;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;color:#fff}h1{font-size:2rem;margin:1rem 0;font-family:'Abril Fatface', serif;letter-spacing:0.1rem;text-align:center;font-weight:400;cursor:pointer}@media screen and (min-width: 420px){h1{font-size:3rem}}@media screen and (min-width: 670px){h1{font-size:5rem;padding-top:1rem}}.icon-menu{font-size:2rem;cursor:pointer;margin:0.5rem 0;z-index:90}.icon-menu:hover{color:silver}nav{width:100%;height:calc(100vh - 145px);display:none}ul{width:100%;height:calc(100% - 145px);display:flex;flex-direction:column;background-color:#000;position:absolute;top:145px}ul li{width:100%;height:100%;font-size:1.5rem;text-align:center;color:#fff;cursor:pointer;display:flex;align-items:center;justify-content:space-around}ul li:hover{background-color:#fff;color:#000;font-weight:600}@media screen and (min-width: 670px){.icon-menu{display:none}nav{display:block !important;z-index:0 !important;height:auto}ul{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-around;background-color:transparent;position:relative;top:0}ul li{width:100%;height:100%;font-size:1.5rem;text-align:center;padding:0.5rem}ul li:hover{background-color:#fff;color:#000}}@media screen and (max-width: 450px){.icon-menu{margin:0.5rem 1rem 0.5rem;align-self:flex-end}}

.container{width:100%;min-height:48vh;display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));grid-gap:1rem;padding:1.5rem;justify-content:center}.no-news{position:absolute;z-index:-1;top:250px;left:50px;font-size:1rem}@media screen and (max-width: 670px){.no-news{top:180px;left:30px}}.box{border:1px solid #969696;background-color:#fff;position:relative;padding-bottom:2rem;box-shadow:0px 0px 5px grey;-webkit-transition-duration:0.3s;transition-duration:0.3s}.box:hover{-webkit-transform:scale(1.01);transform:scale(1.01)}@media screen and (min-height: 780px){.container{min-height:64vh}}@media screen and (min-width: 1000px){.box:first-child{grid-row:1/2;grid-column:1/3}.box:first-child h3{font-size:2.5rem}.box:first-child .image{height:300px}.box:first-child p{font-size:1.5rem}.box:first-child .link{font-size:1rem}}

.title{font-size:1.4rem;text-transform:uppercase;text-align:center}.image{width:100%;height:200px;background-size:cover;background-repeat:no-repeat;background-position:center;margin:0.5rem 0}.title,.description,.link{padding:1rem}.link{position:absolute;bottom:0;right:0}.link a{color:midnightblue;font-weight:600}

.loadingBackground{width:100%;height:100vh;background-color:white;display:flex;align-items:center;justify-content:center;position:absolute;top:0}.loading-message{font-size:3rem}

.search-bar{position:absolute;top:10px;right:10px;font-size:1.5rem;display:flex;align-items:center}.search-bar .search-btn{background-color:transparent;border:none;margin-left:0.5rem;font-size:1.5rem;cursor:pointer}.search-bar .search-icon{color:#fff}.search-bar input{padding:0.5rem;border-radius:10px;border:none;width:12rem}@media screen and (max-width: 670px){.search-bar{top:105px;left:10px}.search-bar input{width:8rem}}@media screen and (max-width: 420px){.search-bar{top:85px;left:10px}.search-bar input{width:8rem}}

footer{width:100%;height:auto;padding:2rem 0 1.5rem;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#000;color:#fff;font-size:0.9rem}footer p{margin-bottom:1rem}footer .heart{position:relative;font-size:1.2rem;top:4px;color:red;-webkit-animation:heartbeat 1.5s ease-in-out infinite both;animation:heartbeat 1.5s ease-in-out infinite both}.icons{width:100%;display:flex;align-items:center;justify-content:center;font-size:2.5rem}.icons .icon{margin:0 0.25rem;color:#fff}.icons .icon:hover{color:silver}@-webkit-keyframes heartbeat{from{-webkit-transform:scale(1);transform:scale(1);-webkit-transform-origin:center center;transform-origin:center center;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out}10%{-webkit-transform:scale(0.91);transform:scale(0.91);-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in}17%{-webkit-transform:scale(0.98);transform:scale(0.98);-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out}33%{-webkit-transform:scale(0.87);transform:scale(0.87);-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in}45%{-webkit-transform:scale(1);transform:scale(1);-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out}}@keyframes heartbeat{from{-webkit-transform:scale(1);transform:scale(1);-webkit-transform-origin:center center;transform-origin:center center;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out}10%{-webkit-transform:scale(0.91);transform:scale(0.91);-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in}17%{-webkit-transform:scale(0.98);transform:scale(0.98);-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out}33%{-webkit-transform:scale(0.87);transform:scale(0.87);-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in}45%{-webkit-transform:scale(1);transform:scale(1);-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out}}

*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}li{list-style:none}a{text-decoration:none}body{width:100%;height:auto;font-family:'Cabin', sans-serif}

